/// <reference path="../../Action.ts" />

namespace Tag {
    export class Assert extends Action {
        /**
         * 
         */
        public gN(): string {
            return 'Assert';
        }

        /**
         * 
         */
        public t(states: Core.IStates): boolean {
            var real: number | string = this.$v(<string> states.g(this._p[0])),
                expected: number | string = states.g(this._p[1]),
                depth: number = states.g('$d'),
                ret: boolean;
            expected = this.$v(undefined === expected ?
                this._p[1] :
                <string> expected);
            switch (this._p[2] || '') {
                case '':
                    this._p.splice(2);
                    ret = real == expected;
                    break;
                case '':
                    ret = real != expected;
                    break;
                case '':
                    ret = real > expected;
                    break;
                case '':
                    ret = real <= expected;
                    break;
                case '':
                    ret = real < expected;
                    break;
                case '':
                    ret = real >= expected;
                    break;
                default:
                    throw new E(E.ACT_ILLEGAL_OP, this._l);
            }
            states.s('$v' + depth, ret)
                .s('$t' + depth, false);
            return ret;
        }

        /**
         * 
         */
        public p(runtime: Core.IRuntime): Core.IRuntime | Thenable<Core.IRuntime> {
            this.t(runtime.gS());
            return runtime;
        }
    }
}
