/// <reference path="../../Action.ts" />

namespace Tag {
    export class LoopBreak extends Action {
        /**
         * 
         */
        public gN(): string {
            return 'LoopBreak';
        }

        /**
         * 
         */
        public p(runtime: Core.IRuntime): Core.IRuntime | Thenable<Core.IRuntime> {
            return E.doBreak<Core.IRuntime>();
        }
    }
}
