/// <reference path="../../Unknown.ts" />

namespace Tag {
    import Util = __Bigine_Util;

    export class SimpPanel extends Unknown {
        /**
         * 
         */
        public gN(): string {
            return 'SimpPanel';
        }

        /**
         * 
         */
        public g(): Util.IHashTable<any> {
            let result: Util.IHashTable<any> = {};
            result['c'] = [];
            Util.each(this._s, (ele: SimpEle) => {
                result['c'].push(ele.g());
            });
            result['n'] = this._c;
            result[''] = 'simp';
            return result;
        }

    }
}
