/// <reference path="Loop.ts" />

namespace Tag {
    import Util = __Bigine_Util;

    export class Otherwise extends Action implements Core.IBlock {
        /**
         * 
         */
        public gN(): string {
            return 'Otherwise';
        }

        /**
         * 
         */
        public t(states: Core.IStates): boolean {
            var key: string = '$t' + states.g('$d');
            if (states.g(key))
                return true;
            states.s(key, true);
            return Util.every(this._s, (tag: Action) => tag.t(states));
        }

        /**
         * 
         */
        public p(runtime: Core.IRuntime): Core.IRuntime | Thenable<Core.IRuntime> {
            var states: Core.IStates = runtime.gS(),
                logger: Util.ILogger = runtime.gL(),
                title: string = 'OTHERWISE',
                kd: string = '$d',
                depth: number = states.g(kd),
                kt: string = '$t' + depth,
                kid: string = '.a',
                id: string = states.g(kid);
            if (!id && states.g(kt))
                return runtime;
            logger.o(title);
            states.s(kt, true)
                .s(kd, 1 + depth);
            return Util.Q.every(this._s, (action: Action) => {
                id = states.g(kid);
                if (id) {
                    if ('gI' in action) {
                        if ((<Idable> action).gI() != id)
                            return runtime;
                        states.d(kid);
                        (<Idable> action).d();
                    } else if ('gA' in action) {
                        if (-1 == Util.indexOf((<Loop> action).gA(), id))
                            return runtime;
                    } else
                        return runtime;
                }
                return action.p(runtime);
            })['catch']((error?: E) => {
                if (error && E.Signal.HALT == error.signal)
                    logger.c(title);
                throw error;
            }).then(() => {
                states.s(kd, depth);
                logger.c(title);
                return runtime;
            });
        }

        /**
         * 
         */
        public gA(): string[] {
            return Loop.prototype.gA.call(this);
        }

        /**
         * 
         */
        public c(): Core.IResource<string | HTMLImageElement>[][] {
            return <Core.IResource<string | HTMLImageElement>[][]> Loop.prototype.c.call(this);
        }
    }
}
