/// <reference path="../Event.ts" />
/// <reference path="IChooseMetas.ts" />

namespace Ev {
    export class Choose extends Event<Core.IChoose> {
        /**
         * 
         */
        public choice: Core.IOptionTag;

        /**
         * 
         */
        constructor(metas: IChooseMetas) {
            super(metas);
            this.choice = metas.choice;
        }

        /**
         * 
         */
        public gT(): string {
            return 'choose';
        }
    }
}
