/// <reference path="../../Action.ts" />
/// <reference path="../../../Core/_Runtime/IDirector.ts" />

namespace Tag {
    export class Stars extends Action {
        /**
         * 
         */
        private _ms: Core.IDirector.Stars;

        /**
         * 
         */
        private _mp: string;

        /**
         * 
         */
        private _mv: string;

        /**
         * 
         */
        constructor(params: string[], content: string, children: Unknown[], lineNo?: number) {
            super(params, content, children, lineNo);
            var stars: typeof Core.IDirector.Stars = Core.IDirector.Stars;
            switch (params[0]) {
                case '':
                    this._ms = stars.OK;
                    this._mp = 'jige';
                    break;
                case '':
                    this._ms = stars.Awesome;
                    this._mp = 'youxiu';
                    break;
                case '':
                    this._ms = stars.Perfect;
                    this._mp = 'wanmei';
                    break;
                case '':
                    this._ms = stars.Superb;
                    this._mp = 'chaojue';
                    break;
                case '':
                    this._ms = stars.Legend;
                    this._mp = 'chuanqi';
                    break;
                default:
                    throw new E(E.ACT_ILLEGAL_STARS, lineNo);
            }
            this._mv = params[1] || '';
        }

        /**
         * 
         */
        public gN(): string {
            return 'Stars';
        }

        /**
         * 
         */
        public p(runtime: Core.IRuntime): Core.IRuntime | Thenable<Core.IRuntime> {
            var states: Core.IStates = runtime.gS(),
                value: string = this.$v(states.g(this._mv)).toString();
            return runtime.gD().stars(this._ms, this._mp, value);
        }
    }
}
