/// <reference path="../../Action.ts" />

namespace Tag {
    export class Tip extends Action {
        /**
         * 
         */
        public gN(): string {
            return 'Tip';
        }

        /**
         * 
         */
        public p(runtime: Core.IRuntime): Core.IRuntime | Thenable<Core.IRuntime> {
            return runtime.gD().tip(runtime.gS().t(this._c));
        }
    }
}
