/// <reference path="../../Action.ts" />

namespace Tag {
    import Util = __Bigine_Util;

    export class Conditions extends Unknown {
        /**
         * 
         */
        public gN(): string {
            return 'Conditions';
        }

        /**
         * 
         */
        public t(states: Core.IStates): boolean {
            return Util.every(this._s, (condition: Action) => condition.t(states));
        }
    }
}
