/// <reference path="../Event.ts" />
/// <reference path="IActionMetas.ts" />

namespace Ev {
    export class Action extends Event<Core.IIdableTag> {
        /**
         * 
         */
        public id: string;

        /**
         * 
         */
        public kind: string;

        /**
         * 
         */
        constructor(metas: IActionMetas) {
            super(metas);
            this.id = metas.target.gI();
            this.kind = metas.target.gN().toLowerCase();
        }

        /**
         * 
         */
        public gT(): string {
            return 'action';
        }
    }
}
