/// <reference path="../Event.ts" />
/// <reference path="ISlotsLoadMetas.ts" />

namespace Ev {
    export class SlotsLoad extends Event<Core.ISlots> {
        /**
         * 
         */
        public id: string;

        /**
         * 
         */
        constructor(metas: ISlotsLoadMetas) {
            super(metas);
            this.id = metas.id;
        }

        /**
         * 
         */
        public gT(): string {
            return 'slots.load';
        }
    }
}
