/// <reference path="../Event.ts" />
/// <reference path="IPayMetas.ts" />

namespace Ev {
    export class Pay extends Event<Core.IStates> {
        /**
         * 
         */
        private amount: number;

        /**
         * id
         */
        private id: string;

        /**
         * 
         */
        private suc: () => void;

        /**
         * 
         */
        private fail: () => void;

        /**
         * 
         */
        constructor(metas: IPayMetas) {
            super(metas);
            this.amount = metas.amount;
            this.suc = metas.suc;
            this.fail = metas.fail;
            this.id = metas.id;
        }

        /**
         * 
         */
        public gT(): string {
            return 'pay';
        }
    }
}
