/// <reference path="../../Action.ts" />

namespace Tag {
    export class Save extends Action {
        /**
         * 
         */
        public gN(): string {
            return 'Save';
        }

        /**
         * 
         */
        public p(runtime: Core.IRuntime): Core.IRuntime | Thenable<Core.IRuntime> {
            var scene: Unknown = this.gU(),
                brief: string = this._p[0];
            if (!brief) {
                while ('Scene' != scene.gN())
                    scene = scene.gU();
                brief = scene.$c();
            }
            runtime.gS().e(false);
            return runtime;
        }
    }
}
