/// <reference path="../Entity.ts" />
/// <reference path="Image.ts" />

namespace Tag {
    export class DefCG extends Entity {
        /**
         * 
         */
        public gN(): string {
            return 'DefCG';
        }

        /**
         * 
         */
        public o(): Core.IResource<HTMLImageElement> {
            return (<Image> this.$q('Image')[0]).o();
        }
    }
}
