/// <reference path="../Entity.ts" />
/// <reference path="Audio.ts" />

namespace Tag {
    export class DefSE extends Entity {
        /**
         * 
         */
        public gN(): string {
            return 'DefSE';
        }

        /**
         * 
         */
        public gT(): Core.IEpisode.Entity {
            return Core.IEpisode.Entity.SE;
        }

        /**
         * 
         */
        public o(): Core.IResource<string> {
            return (<Audio> this.$q('Audio')[0]).o();
        }
    }
}
