/// <reference path="../Event.ts" />
/// <reference path="IDonateMetas.ts" />

namespace Ev {
    export class Donate extends Event<Core.IStates> {
        /**
         * 
         */
        private amount: number;

        /**
         * 
         */
        private suc: () => void;

        /**
         * 
         */
        private fail: () => void;

        /**
         * 
         */
        constructor(metas: IDonateMetas) {
            super(metas);
            this.amount = metas.amount;
            this.suc = metas.suc;
            this.fail = metas.fail;
        }

        /**
         * 
         */
        public gT(): string {
            return 'donate';
        }
    }
}
