/// <reference path="../Event.ts" />
/// <reference path="IErrorMetas.ts" />

namespace Ev {
    export class Error extends Event<any> {
        /**
         * 
         */
        public error: Error;

        /**
         * 
         */
        constructor(metas: IErrorMetas) {
            super(metas);
            this.error = metas.error;
        }

        /**
         * 
         */
        public gT(): string {
            return 'error';
        }
    }
}
