/// <reference path="../../Action.ts" />

namespace Tag {
    export class StopBGM extends Action {
        /**
         * 
         */
        public gN(): string {
            return 'StopBGM';
        }

        /**
         * 
         */
        public p(runtime: Core.IRuntime): Core.IRuntime | Thenable<Core.IRuntime> {
            runtime.gS().d('_b');
            return runtime.gD().playBGM();
        }
    }
}
